:root{--max-content-width: 1280px;--default-color: #fff;--active-color: #3c59f0;--inactive-color: #626262;--background-color: #151419;--background-start-color: #1b1a1f;--background-stop-color: #111013}.header{position:fixed;z-index:10;top:0;right:0;left:0;height:60px;background-color:var(--background-color);box-shadow:0 0 16px 0 rgba(0,0,0,0.55);transition:height .15s}@media (min-width: 46.25em){.header{height:100px}}.content{margin:0 auto;padding:0 20px;display:flex;align-items:center;justify-content:space-between;max-width:var(--max-content-width);height:100%}@media (min-width: 46.25em){.content{padding:0 60px}}.logo{width:118px;color:var(--active-color)}.logo:global(.router-link-active){cursor:default}.nav{display:flex;align-items:center;font-size:16px;color:var(--inactive-color);text-decoration:none;transition:color .15s}.nav:global(:not(.router-link-active)):hover,.nav:global(:not(.router-link-active)):focus{color:var(--default-color)}.nav:global(.router-link-active){color:var(--default-color)}.nav .icon{margin:0 0 0 10px;width:16px}
