:root{--max-content-width: 1280px;--default-color: #fff;--active-color: #3c59f0;--inactive-color: #626262;--background-color: #151419;--background-start-color: #1b1a1f;--background-stop-color: #111013}.content{margin:0 auto;padding:20px;max-width:var(--max-content-width);height:100%}@media (min-width: 46.25em){.content{padding:50px 60px}}.controls{display:flex}.sort{margin-left:auto;color:var(--inactive-color);transition:color .15s}.sort .sortLabel{display:flex;align-items:center;cursor:pointer;user-select:none}.sort .sortLabel:hover{color:var(--default-color)}.sort .sortLabel :global(.checkbox){margin-left:15px}.select{margin-right:20px;position:relative;display:inline-block;vertical-align:top;height:24px;cursor:pointer;user-select:none}.select:before{content:'';position:absolute;right:-2px;bottom:0;left:0;height:1px;background-image:linear-gradient(to right, var(--default-color) 50%, transparent 50%);background-size:6px 1px}.select .chevron{vertical-align:middle;margin-left:10px;width:9px;transition:transform .15s}.select:focus-within{color:var(--active-color)}.select:focus-within:before{background-image:linear-gradient(to right, var(--active-color) 50%, transparent 50%)}.selectExpanded .chevron{transform:rotate(180deg)}.dropdown{overflow:hidden;display:flex;flex-wrap:wrap;align-items:center;max-height:0;transition:max-height 0.25s cubic-bezier(0, 1, 0, 1);user-select:none}.dropdownExpanded{max-height:10000px;transition:max-height .5s ease-in-out}.options{padding:30px 0 0;display:flex;flex-wrap:wrap;opacity:0;visibility:hidden;transition:opacity .15s, transform .15s;transform:translateY(-15px)}.dropdownExpanded .options{opacity:1;visibility:visible;transform:translateY(0)}.key{margin:0 15px 0 0;height:24px;font-size:20px;color:var(--inactive-color);text-transform:uppercase}.link{margin:0 15px 18px 0;padding:4px 12px;display:flex;align-items:center;height:24px;font-size:12px;color:var(--inactive-color);text-transform:capitalize;border:1px solid var(--inactive-color);border-radius:24px;transition:color .15s, border-color .15s;text-decoration:none}.link:global(:not(.router-link-active)):hover,.link:global(:not(.router-link-active)):focus{color:var(--default-color);border-color:var(--default-color)}.link:global(.router-link-active){color:var(--active-color);border-color:var(--active-color)}.link:global(.router-link-active):focus{color:var(--default-color);border-color:var(--default-color)}.link:focus{outline:none}.link .close{margin-left:10px}.link.linkLast{margin-right:40px}.divider{background:none;border:none;width:100%}.joiner{display:flex}
