:root{--max-content-width: 1280px;--default-color: #fff;--active-color: #3c59f0;--inactive-color: #626262;--background-color: #151419;--background-start-color: #1b1a1f;--background-stop-color: #111013}.checkbox{color:currentColor}.input{position:absolute;visibility:hidden;opacity:0}.switch{position:relative;width:32px;height:16px;display:flex;align-items:center;border:1px solid;border-radius:15px;cursor:pointer}.tick{position:absolute;top:2px;right:auto;left:2px;width:10px;height:10px;border-radius:10px;background-color:currentColor;transition:left .25s ease-in-out}.switchChecked .tick{left:18px}
