:root{--max-content-width: 1280px;--default-color: #fff;--active-color: #3c59f0;--inactive-color: #626262;--background-color: #151419;--background-start-color: #1b1a1f;--background-stop-color: #111013}.filled{opacity:0;transform:scale(0.25);transform-origin:50%;transition:opacity .15s, transform .35s}.filledVisible{opacity:1;transform:scale(1)}
