:root{--max-content-width: 1280px;--default-color: #fff;--active-color: #3c59f0;--inactive-color: #626262;--background-color: #151419;--background-start-color: #1b1a1f;--background-stop-color: #111013}.content{margin:0 auto;padding:0 20px;max-width:var(--max-content-width);height:100%}@media (min-width: 46.25em){.content{padding:0 60px}}.list{margin:0 -15px;display:flex;flex-wrap:wrap;align-items:center}.item{box-sizing:content-box;margin:0 0 30px;display:flex;position:relative;flex-basis:50%;height:200px;font-size:25px}@media (min-width: 46.25em){.item{flex-basis:33.33%;height:290px}}.item:first-of-type{flex-basis:100%;height:520px;font-size:30px}.wrapper{position:relative;overflow:hidden;width:100%;height:100%;margin:0 15px;border-radius:8px}.wrapper .image{width:100%;height:100%;object-fit:cover;opacity:1;transition:opacity .5s ease-in-out}.wrapper .image:not([src]){opacity:0}.wrapper .name{box-sizing:border-box;position:absolute;top:0;right:0;bottom:0;left:0;padding:25px;display:flex;align-items:flex-end;justify-content:flex-end;width:100%;height:100%;text-align:right;font-weight:600;text-transform:capitalize;background-image:linear-gradient(to bottom, rgba(0,0,0,0) 50%, #000)}.favourite{position:absolute;top:0;left:0;padding:25px;cursor:pointer;transform:scale(1);transition:transform .15s ease-in-out}.favourite .icon{width:30px}.favourite:hover{transform:scale(1.15)}.progress{padding:50px 0;margin:0 auto;display:flex;align-items:center;justify-content:center;color:var(--default-color);visibility:hidden;opacity:0}.progress .tick{margin:0 5px;width:10px;height:10px;border:1px solid;border-radius:10px;transition:transform .15s ease-in-out;animation-name:zoom;animation-duration:1s;animation-iteration-count:infinite;animation-fill-mode:both}.progress .tick:nth-child(2){animation-delay:.2s}.progress .tick:nth-child(3){animation-delay:.4s}.progressActive{visibility:visible;opacity:1}@keyframes zoom{0%{transform:scale(1)}20%{transform:scale(1.5)}100%{transform:scale(1)}}
