:root{--max-content-width: 1280px;--default-color: #fff;--active-color: #3c59f0;--inactive-color: #626262;--background-color: #151419;--background-start-color: #1b1a1f;--background-stop-color: #111013}.content{position:fixed;right:0;left:0;bottom:100px;margin:0 auto;max-width:var(--max-content-width);transition:opacity .15s;color:var(--defaultColor);visibility:hidden;opacity:0}.contentVisible{visibility:visible;opacity:1}.button{position:absolute;right:60px;width:55px;height:55px;display:flex;align-items:center;justify-content:center;background-color:var(--background-color);border:1px solid;border-radius:55px;cursor:pointer;transition:color .15s, border-color .15s}.button:hover{color:var(--active-color);border-color:var(--active-color)}.icon{width:20px}
